body{
  background: #f0f0f0;
}
.App{
  height: 100%;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  width: 100%;
  background: #ffffff;
  border: 2px solid black
}

nav{
  display: block;

  background: rgb(190, 190, 190);
  width: 100%;
  height: 75px
}
nav ul{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
nav ul li,a{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0 10px;
  width: 200px;
  font-size: 36px;
}

li a:hover{
  background: rgba(255, 255, 255, 0.377);
}


.Home{
  min-height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}