* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body ,#root{
    height: 100vh;
}
:root {
  font-size: 16px;
  font-family: 'Open Sans';
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #b6b6b6;
  --bg-secondary: #ececec;
}
a {
  color: #161616;
  text-decoration: none;
  font-size: 16px;
  font-family: 'Open Sans';
}
li {
  list-style-type: none;
}
